<template>
  <div id="contact">
    <custom-header :heading="heading" :subheading="subheading" :image="image"></custom-header>

    <!-- Main Content -->
    <div class="container">
      <div class="row">
        <div class="col-lg-8 col-md-10 mx-auto">
          <p>Want to get in touch? Fill out the form below to send me a message and I will get back to you as soon as possible!</p>
          <flash-message class="myCustomClass"></flash-message>
          <form name="sentMessage" id="contactForm" @submit.prevent="formSubmit" validate>
            <!-- {{ csrf_field|safe }} -->
            <div class="control-group">
              <div class="form-group floating-label-form-group controls">
                <label>Name</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder="Name"
                  id="name"
                  name="name"
                  required
                  data-validation-required-message="Please enter your name."
                  v-model="name"
                >
                <p class="help-block text-danger"></p>
              </div>
            </div>
            <div class="control-group">
              <div class="form-group floating-label-form-group controls">
                <label>Email Address</label>
                <input
                  type="email"
                  class="form-control"
                  placeholder="Email Address"
                  id="email"
                  name="email"
                  required
                  data-validation-required-message="Please enter your email address."
                  v-model="email"
                >
                <p class="help-block text-danger"></p>
              </div>
            </div>
            <div class="control-group">
              <div class="form-group floating-label-form-group controls">
                <label>Message</label>
                <textarea
                  rows="5"
                  class="form-control"
                  placeholder="Message"
                  id="message"
                  name="message"
                  required
                  data-validation-required-message="Please enter a message."
                  v-model="message"
                ></textarea>
                <p class="help-block text-danger"></p>
              </div>
            </div>
            <br>
            <div id="success"></div>
            <div class="form-group">
              <button type="submit" class="btn btn-primary" id="sendMessageButton">Send</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Import Libraries
const axios = require("axios");

export default {
  data() {
    return {
      name: "",
      email: "",
      message: "",
      heading: "Contact Me",
      subheading: "Have questions? I have answers.",
      image: "blog/img/contact-bg.jpg"
    };
  },

  methods: {
    formSubmit(e) {
      e.preventDefault();

      var postData = JSON.stringify({
        name: this.name,
        email: this.email,
        message: this.message
      });

      let axiosConfig = {
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*"
        },
        emulateJSON: true
      };

      axios
        .post("http://localhost:8000/api/contact", postData, axiosConfig)
        .then(response => {
          this.flash("Email sent", "success");
        })
        .catch(error => {
          this.flash("Try again", "error");
        });
    }
  }
};
</script>
